import { useDebounce } from '@hooks/useDebounce';
import { useOutsideClick } from '@hooks/useOutsideClick';
import { selectFilterResultState } from '@store/selectors/currencySelector';
import { filterCurrencies } from '@store/slices/currencySlice';
import { ChangeEvent, useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { Currency } from '@/types/financial';

export const useCurrencySearch = (
  excludedCurrency: string,
  onCurrencyChange: (currencyId: string) => void,
) => {
  const [searchText, setSearchText] = useState<string>('');
  const [isDropdown, setIsDropdown] = useState<boolean>(false);

  const dispatch = useDispatch();
  const filteredCurrencies = useSelector(selectFilterResultState);

  const searchItem = useDebounce({ value: searchText, delay: 300 });
  const dropdownRef = useRef<HTMLUListElement | null>(null);

  const handleTextChange = (event: ChangeEvent<HTMLInputElement>) => {
    setSearchText(event.target.value);
  };

  const handleDropdownClose = () => setIsDropdown(false);
  const handleDropdownOpen = () => setIsDropdown(true);

  const handleCurrencySelect = (currency: Currency) => () => {
    setSearchText(currency.name);
    handleDropdownClose();
    onCurrencyChange(currency.asset_id);
  };

  useOutsideClick({
    ref: dropdownRef,
    handler: handleDropdownClose,
  });

  useEffect(() => {
    dispatch(filterCurrencies({ searchItem, excludedCurrency }));
  }, [searchItem, excludedCurrency, dispatch]);

  return {
    searchText,
    isDropdown,
    filteredCurrencies,
    handleTextChange,
    handleCurrencySelect,
    handleDropdownOpen,
    dropdownRef,
  };
};
