import { useAppDispatch, useAppSelector } from '@store/hooks';
import { selectConversionState } from '@store/selectors/currencySelector';
import { fetchExchangeRateThunk } from '@store/slices/currencySlice';

import { FetchExchangeRate } from '@/types/financial';

export const useExchange = () => {
  const dispatch = useAppDispatch();
  const { conversionResult, isConverting, conversionError } = useAppSelector(
    selectConversionState,
  );
  const fetchExchangeRate = ({
    baseCurrency,
    amount,
    targetCurrency,
  }: FetchExchangeRate) => {
    dispatch(fetchExchangeRateThunk({ baseCurrency, amount, targetCurrency }));
  };

  return {
    conversionResult,
    isConverting,
    conversionError,
    fetchExchangeRate,
  };
};
