import { RootState } from '@/store';

export const selectCurrenciesState = (state: RootState) => ({
  data: state.currencies.currencies,
  isLoading: state.currencies.isLoading,
  error: state.currencies.error,
  lastUpdated: state.currencies.lastUpdated,
});

export const selectConversionState = (state: RootState) => ({
  conversionResult: state.currencies.conversionResult,
  isConverting: state.currencies.isConverting,
  conversionError: state.currencies.conversionError,
});

export const selectFilterResultState = (state: RootState) =>
  state.currencies.filterResult;
