import IFIX from '@images/stocks/IFIX.svg';

import { CardContainer, Image, Info, Rate, Title } from '../styled';
import { StockCardProps } from './types';

export const StockCard = ({ financialItem }: StockCardProps) => {
  const { name, rate, image } = financialItem;

  const cardImage = image ?? IFIX;
  const formattedRate = rate + '%';

  return (
    <CardContainer>
      <Image src={cardImage} alt={name} />
      <Info>
        <Title>{name}</Title>
        <Rate>{formattedRate}</Rate>
      </Info>
    </CardContainer>
  );
};
