import Dollar from '@images/quotes/Dollar.svg';

import { CardContainer, Image, Info, Rate, Title } from '../styled';
import { CurrencyCardProps } from './types';

export const CurrencyCard = ({ financialItem }: CurrencyCardProps) => {
  const { name, price_usd, icon_url } = financialItem;

  const cardImage = icon_url ?? Dollar;
  const formattedPrice = '$ ' + price_usd.toFixed(2);

  return (
    <CardContainer>
      <Image src={cardImage} alt={name} />
      <Info>
        <Title>{name}</Title>
        <Rate>{formattedPrice}</Rate>
      </Info>
    </CardContainer>
  );
};
