import { styled } from 'styled-components';

export const Overlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;

  background: ${({ theme }) => theme.colors.modalBackground};
`;

export const ModalContainer = styled.div`
  background: ${({ theme }) => theme.colors.modalCardBackground};
  padding: ${({ theme }) => theme.spacing.l};
  border-radius: 5px;

  max-width: ${({ theme }) => theme.size.elementSize.s};
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    max-width: ${({ theme }) => theme.size.elementSize.s};
    padding: ${({ theme }) => theme.spacing.s};
    margin: 0 ${({ theme }) => theme.spacing.l};
  }
`;

export const Image = styled.img`
  width: ${({ theme }) => theme.size.imageSize.l};
  height: ${({ theme }) => theme.size.imageSize.l};
`;

export const Info = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;

  margin-top: ${({ theme }) => theme.spacing.m};

  max-width: ${({ theme }) => theme.size.elementSize.s};
  width: 100%;

  background: ${({ theme }) => theme.colors.background};
  padding: ${({ theme }) => theme.spacing.s};
  border-radius: 5px;

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    max-width: ${({ theme }) => theme.size.elementSize.s};
  }
`;

export const Title = styled.h3`
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.s};
  }
`;

export const Rate = styled.p`
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  color: ${({ theme }) => theme.colors.cardText};

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.s};
  }
`;

export const ResultContainer = styled.div`
  max-width: ${({ theme }) => theme.size.elementSize.s};
  width: 100%;

  background: ${({ theme }) => theme.colors.background};
  padding: ${({ theme }) => theme.spacing.s};
`;

export const ResultSubscription = styled.p`
  font-size: ${({ theme }) => theme.font.fontSizes.xs};
  color: ${({ theme }) => theme.colors.text};
`;

export const ResultText = styled.p`
  font-size: ${({ theme }) => theme.font.fontSizes.s};
  color: ${({ theme }) => theme.colors.primaryText};
`;

export const Button = styled.button`
  margin-top: ${({ theme }) => theme.spacing.s};
  padding: ${({ theme }) => theme.spacing.s};

  background: ${({ theme }) => theme.colors.background};

  border-radius: 5px;

  max-width: ${({ theme }) => theme.size.elementSize.xs};
  width: 100%;

  text-align: center;
  font-size: ${({ theme }) => theme.font.fontSizes.s};

  color: ${({ theme }) => theme.colors.primaryText};

  cursor: pointer;

  transition: transform 0.3s ease;

  &:hover {
    transform: scale(95%);
  }

  &:disabled {
    opacity: 0.5;
  }

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    max-width: ${({ theme }) => theme.size.elementSize.s};
  }
`;

export const ButtonContainer = styled.div`
  display: flex;
  justify-content: space-between;
  max-width: ${({ theme }) => theme.size.elementSize.s};
  width: 100%;

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    flex-direction: column;
  }
`;
