import { ChangeEvent, memo, useCallback, useEffect, useState } from 'react';

import {
  defaultCharactersAmount,
  ErrorMessages,
  inputSubscription,
  validationRegex,
} from './config';
import { ErrorMessage, Input, InputContainer, Subscription } from './styled';
import { CurrencyAmountInputProps } from './types';

const CurrencyAmountInput = (props: CurrencyAmountInputProps) => {
  const { value, onAmountChange, maxLength = defaultCharactersAmount } = props;

  const [inputValue, setInputValue] = useState<string>('');
  const [errorMessage, setErrorMessage] = useState<string | null>(null);

  const handleChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      const value = event.target.value;

      if (value.length > maxLength) {
        setErrorMessage(`${ErrorMessages.maxValue} ${maxLength}`);
        return;
      }

      if (value === '' || validationRegex.test(value)) {
        setErrorMessage(null);
        setInputValue(value);
        onAmountChange(value);
      } else {
        setErrorMessage(ErrorMessages.validAmount);
        setInputValue(value);
      }
    },
    [maxLength, onAmountChange],
  );

  useEffect(() => {
    if (value !== inputValue) {
      setInputValue(value);
    }
  }, [value, inputValue]);

  return (
    <InputContainer>
      {errorMessage ? (
        <ErrorMessage data-testid="input-error-field">
          {errorMessage}
        </ErrorMessage>
      ) : (
        <Subscription data-testid="input-subscription-field">
          {inputSubscription}
        </Subscription>
      )}
      <Input
        data-testid="currency-amount-input"
        type="text"
        value={inputValue}
        onChange={handleChange}
        maxLength={maxLength}
      />
    </InputContainer>
  );
};

export const MemoizedCurrencyAmountInput = memo(CurrencyAmountInput);
