import { MemoizedCurrencySearch as CurrencySearch } from '@components/CurrencySearch';
import { MemoizedCurrencyAmountInput as CurrencyAmountInput } from '@components/UI/CurrencyAmoutInput';
import { useDisableButton } from '@hooks/useDisableButton';
import { useExchange } from '@hooks/useExchange';
import { useResetConversion } from '@hooks/useResetConversion';
import Dollar from '@images/quotes/Dollar.svg';
import { useCallback, useState } from 'react';
import { createPortal } from 'react-dom';

import { ButtonsText } from './config';
import {
  Button,
  ButtonContainer,
  Image,
  Info,
  ModalContainer,
  Overlay,
  Rate,
  ResultContainer,
  ResultSubscription,
  ResultText,
  Title,
} from './styled';
import { ExchangeModalProps } from './types';

export const ExchangeModal = (props: ExchangeModalProps) => {
  const {
    onClose,
    currency: { asset_id, name, icon_url, price_usd },
    modalRef,
  } = props;

  const cardImage = icon_url ?? Dollar;
  const formattedPrice = '$ ' + price_usd.toFixed(2);

  const [selectedCurrency, setSelectedCurrency] = useState<string>('');
  const [currencyAmount, setCurrencyAmount] = useState<string>('1');

  const { resetConversion } = useResetConversion();

  const { conversionResult, isConverting, conversionError, fetchExchangeRate } =
    useExchange();

  const numericCurrencyAmount = parseFloat(currencyAmount);

  const isButtonDisabled = useDisableButton([
    !!selectedCurrency,
    numericCurrencyAmount > 0,
    !isNaN(numericCurrencyAmount),
  ]);

  const handleCurrencyChange = useCallback(
    (currencyName: string) => {
      setSelectedCurrency(currencyName);
      resetConversion();
    },
    [resetConversion],
  );

  const handleAmountChange = useCallback((newCurrencyAmount: string) => {
    setCurrencyAmount(newCurrencyAmount);
  }, []);

  const handleConvert = () => {
    if (
      selectedCurrency &&
      numericCurrencyAmount > 0 &&
      !isNaN(numericCurrencyAmount)
    ) {
      fetchExchangeRate({
        baseCurrency: asset_id,
        amount: numericCurrencyAmount,
        targetCurrency: selectedCurrency,
      });
    }
  };

  return createPortal(
    <Overlay>
      <ModalContainer ref={modalRef}>
        <Image src={cardImage} alt={name} />
        <Info>
          <Title>{name}</Title>
          <Rate>{formattedPrice}</Rate>
        </Info>

        <CurrencySearch
          excludedCurrency={name}
          onCurrencyChange={handleCurrencyChange}
        />

        <CurrencyAmountInput
          value={currencyAmount}
          onAmountChange={handleAmountChange}
        />

        {isConverting && (
          <ResultContainer data-testid="converting-loader">
            <ResultText>Converting...</ResultText>
          </ResultContainer>
        )}

        {conversionResult && (
          <ResultContainer data-testid="converting-block">
            <ResultSubscription>{asset_id} =</ResultSubscription>
            <ResultText>
              {conversionResult} {selectedCurrency}
            </ResultText>
          </ResultContainer>
        )}

        {conversionError && (
          <ResultContainer data-testid="converting-error">
            <ResultText>{conversionError}</ResultText>
          </ResultContainer>
        )}

        <ButtonContainer>
          <Button
            data-testid="convert-button"
            onClick={handleConvert}
            disabled={isButtonDisabled}
          >
            {ButtonsText.convertButtonText}
          </Button>
          <Button onClick={onClose}>{ButtonsText.closeButtonText}</Button>
        </ButtonContainer>
      </ModalContainer>
    </Overlay>,
    document.body,
  );
};
