import { styled } from 'styled-components';

export const Container = styled.section`
  margin: ${({ theme }) => theme.spacing.xl} 0;

  display: flex;
  justify-content: space-between;
  align-items: center;

  background: ${({ theme }) => theme.colors.gradientBackground};

  padding: ${({ theme }) => theme.spacing.xl};

  border-radius: 15px;

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    flex-direction: column;
  }
`;

export const TitleContainer = styled.div`
  text-align: end;

  max-width: ${({ theme }) => theme.size.elementSize.l};
  width: 100%;

  display: flex;
  flex-direction: column;

  margin-right: ${({ theme }) => theme.spacing['3xl']};

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    text-align: center;
    margin: 0;
  }
`;

export const Title = styled.h1`
  font-size: ${({ theme }) => theme.font.fontSizes['3xl']};
  font-weight: ${({ theme }) => theme.font.fontWeights.bold};

  background: ${({ theme }) => theme.colors.gradientText};

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    font-size: ${({ theme }) => theme.font.fontSizes['2xl']};
  }
`;

export const Subtitle = styled.h3`
  font-size: ${({ theme }) => theme.font.fontSizes.l};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};

  color: ${({ theme }) => theme.colors.primaryText};

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    margin: ${({ theme }) => theme.spacing.l} 0;
  }
`;

export const Image = styled.img`
  width: ${({ theme }) => theme.size.imageSize['3xl']};
  height: ${({ theme }) => theme.size.imageSize['3xl']};

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    width: ${({ theme }) => theme.size.imageSize['2xl']};
    height: ${({ theme }) => theme.size.imageSize['2xl']};
  }
`;
