import { styled } from 'styled-components';

export const CardContainer = styled.div`
  display: flex;
  align-items: center;

  border: 1px solid ${({ theme }) => theme.colors.cardBorder};
  border-radius: 5px;

  background: ${({ theme }) => theme.colors.cardBackground};

  padding: ${({ theme }) => theme.spacing.l};

  cursor: pointer;

  transition: transform 0.3s ease;

  &:hover {
    transform: scale(95%);
  }

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    padding: ${({ theme }) => theme.spacing.m};
  }

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    padding: ${({ theme }) => theme.spacing.s};
  }
`;

export const Image = styled.img`
  width: ${({ theme }) => theme.size.imageSize.m};
  height: ${({ theme }) => theme.size.imageSize.m};
`;

export const Info = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  margin-left: ${({ theme }) => theme.spacing.m};

  max-width: ${({ theme }) => theme.size.elementSize.xs};
  width: 100%;
  height: 100%;

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    max-width: ${({ theme }) => theme.size.elementSize['2xs']};
  }
`;

export const Title = styled.h3`
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.s};
  }
`;

export const Rate = styled.p`
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  color: ${({ theme }) => theme.colors.cardText};

  margin-top: ${({ theme }) => theme.spacing.s};

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.s};

    margin-top: ${({ theme }) => theme.spacing.xs};
  }
`;
