import { styled } from 'styled-components';

export const InputContainer = styled.div`
  position: relative;
  max-width: ${({ theme }) => theme.size.elementSize.s};

  width: 100%;
`;

export const Subscription = styled.p`
  padding: ${({ theme }) => theme.spacing.s};

  font-size: ${({ theme }) => theme.font.fontSizes.s};

  color: ${({ theme }) => theme.colors.primaryText};
`;

export const Input = styled.input`
  width: 100%;
  padding: ${({ theme }) => theme.spacing.s};

  border-radius: 5px;

  font-size: ${({ theme }) => theme.font.fontSizes.s};

  background: ${({ theme }) => theme.colors.background};
  color: ${({ theme }) => theme.colors.primaryText};

  &:focus {
    border: 2px solid ${({ theme }) => theme.colors.activeLink};
  }
`;

export const Dropdown = styled.ul`
  position: absolute;
  z-index: 1;

  width: 100%;
  max-height: ${({ theme }) => theme.size.elementSize.xs};
  overflow-y: auto;

  border-radius: 5px;

  margin-top: ${({ theme }) => theme.spacing.s};
`;

export const DropdownItem = styled.li`
  padding: ${({ theme }) => theme.spacing.s};

  background-color: ${({ theme }) => theme.colors.background};
  color: ${({ theme }) => theme.colors.primaryText};

  font-size: ${({ theme }) => theme.font.fontSizes.s};
  cursor: pointer;

  &:hover {
    background-color: ${({ theme }) => theme.colors.activeLink};
  }

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    font-size: ${({ theme }) => theme.font.fontSizes.xs};
  }
`;
