import { CurrencyCard } from '@components/FinancialCard/CurrencyCard';
import { StockCard } from '@components/FinancialCard/StockCard';
import { MainLoader } from '@components/Loaders/MainLoader';
import { ExchangeModal } from '@components/Modals/Exchange';
import { useCachableStoreRequest } from '@hooks/useCachableStoreRequest';
import { useModal } from '@hooks/useModal';
import { selectCurrenciesState } from '@store/selectors/currencySelector';
import { selectStocksState } from '@store/selectors/stockSelector';
import { fetchCurrenciesThunk } from '@store/slices/currencySlice';
import { useState } from 'react';
import { useSelector } from 'react-redux';

import { Currency } from '@/types/financial';

import { sectionTitles, updatedTitle } from './config';
import { FinancialSection, GridContainer, SectionTitle, Timer } from './styled';

export const Financial = () => {
  const stocks = useSelector(selectStocksState);
  const {
    data: currencies,
    isLoading,
    error,
    lastUpdated,
  } = useCachableStoreRequest<Currency[]>({
    cacheInterval: 60 * 1000,
    selector: selectCurrenciesState,
    thunk: fetchCurrenciesThunk,
  });
  const { isOpen, handleOpen, handleClose, modalRef } = useModal();
  const [selectedCurrency, setSelectedCurrency] = useState<Currency | null>(
    null,
  );

  const handleCardClick = (currency: Currency) => {
    setSelectedCurrency(currency);
    handleOpen();
  };

  if (error) {
    return <p data-testid="financial-error-message">{error}</p>;
  }

  if (isLoading) {
    return <MainLoader />;
  }

  return (
    <FinancialSection>
      <Timer>
        {updatedTitle} {lastUpdated}
      </Timer>

      <SectionTitle>{sectionTitles.stocks}</SectionTitle>
      {stocks && (
        <GridContainer>
          {stocks.map((stock) => (
            <StockCard key={stock.id} financialItem={stock} />
          ))}
        </GridContainer>
      )}

      <SectionTitle>{sectionTitles.quotes}</SectionTitle>
      {currencies && (
        <GridContainer>
          {currencies.map((currency) => (
            <div
              key={currency.asset_id}
              onClick={() => handleCardClick(currency)}
            >
              <CurrencyCard financialItem={currency} />
            </div>
          ))}
        </GridContainer>
      )}

      {isOpen && selectedCurrency && (
        <ExchangeModal
          onClose={handleClose}
          currency={selectedCurrency}
          modalRef={modalRef}
        />
      )}
    </FinancialSection>
  );
};
