import { styled } from 'styled-components';

export const FinancialSection = styled.section`
  max-width: ${({ theme }) => theme.size.elementSize.xl};
  width: 100%;
`;

export const SectionTitle = styled.h2`
  font-size: ${({ theme }) => theme.font.fontSizes.m};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};

  margin: ${({ theme }) => theme.spacing.xl} 0;

  padding-bottom: ${({ theme }) => theme.spacing.s};

  max-width: ${({ theme }) => theme.size.elementSize.s};
  width: 100%;

  border-bottom: 2px solid ${({ theme }) => theme.colors.cardBorder};
`;

export const GridContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    grid-template-columns: 1fr;
    gap: 20px;
  }
`;

export const TimerContainer = styled.div`
  display: flex;
  justify-content: space-between;

  max-width: ${({ theme }) => theme.size.elementSize.xs};
`;

export const Timer = styled.h1`
  text-align: center;

  font-size: ${({ theme }) => theme.font.fontSizes.m};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};
`;
