import { useCurrencySearch } from '@hooks/useCurrencySearch';
import { memo } from 'react';

import { inputSubscription, placeholder } from './config';
import {
  Dropdown,
  DropdownItem,
  Input,
  InputContainer,
  Subscription,
} from './styled';
import { CurrencySearchProps } from './types';

const CurrencySearch = (props: CurrencySearchProps) => {
  const { excludedCurrency, onCurrencyChange } = props;

  const {
    searchText,
    isDropdown,
    filteredCurrencies,
    handleTextChange,
    handleCurrencySelect,
    handleDropdownOpen,
    dropdownRef,
  } = useCurrencySearch(excludedCurrency, onCurrencyChange);

  return (
    <InputContainer>
      <Subscription>{inputSubscription}</Subscription>
      <Input
        data-testid="currency-select-input"
        type="text"
        placeholder={placeholder}
        value={searchText}
        onChange={handleTextChange}
        onFocus={handleDropdownOpen}
      />
      <Dropdown ref={dropdownRef}>
        {isDropdown &&
          filteredCurrencies.map((currency) => (
            <DropdownItem
              key={currency.asset_id}
              onClick={handleCurrencySelect(currency)}
            >
              {currency.name}
            </DropdownItem>
          ))}
      </Dropdown>
    </InputContainer>
  );
};

export const MemoizedCurrencySearch = memo(CurrencySearch);
