import CurrencyTrackerLogo from '@images/CurrencyTrackerLogo.svg';

import { pageHeaderData } from './config';
import { Container, Image, Subtitle, Title, TitleContainer } from './styled';

export const PageHeader = () => {
  return (
    <Container>
      <TitleContainer>
        <Title>{pageHeaderData.title}</Title>
        <Subtitle>{pageHeaderData.subtitle}</Subtitle>
      </TitleContainer>
      <Image src={CurrencyTrackerLogo} />
    </Container>
  );
};
